import { ShowPopupProps } from '../../ontactsComponent';

export const PopupSuccess = ({ setIsShowPopup }: ShowPopupProps) => {
  return (
    <PopupSuccessStl>
      <PopupSuccessWindow>
        <PopupSuccessTitle>  !</PopupSuccessTitle>
        <PopupSuccessText>
          -   ,   , - ,
            .
        </PopupSuccessText>
        <PopupSuccessCloseButton onClick={() => setIsShowPopup(false)}>
           
        </PopupSuccessCloseButton>
      </PopupSuccessWindow>
    </PopupSuccessStl>
  );
};
