import React from "react";
import logo from "../../../../assets/icon/logo.png";
import { NavLink } from "react-router-dom";
import ContentContainer from "../../../UI/ContentContainer";
import Button from "../../../UI/Button";
 
import { BackgroundColor, BottomNav } from "./BottomHeaderStyle";
import IconMap from "../../../../assets/icon/IconMap";

export default function BottomHeader() {

  return (
    <BackgroundColor>
      <ContentContainer>
        <img src={logo} alt="logo" />
        <BottomNav>
          <li><NavLink to="/j" className={({ isActive }) => (isActive ? "active link" : "link")} >   {<IconMap fill='#FFD54F'></IconMap>}</NavLink> </li>
          <li><NavLink to="/k" className={({ isActive }) => (isActive ? "active link" : "link")} >  </NavLink> </li>
          <li><NavLink to="/l" className={({ isActive }) => (isActive ? "active link" : "link")} >  </NavLink> </li>
          <li><NavLink to="/m" className={({ isActive }) => (isActive ? "active link" : "link")} >  </NavLink> </li>
        </BottomNav>
        <Button>+   </Button>
      </ContentContainer>
    </BackgroundColor>
  );
}
